document.addEventListener("DOMContentLoaded",(function(){const e=fetch("../resources/data/data.json").then((e=>e.json()));document.querySelector(".main__btn").addEventListener("click",(function(){e.then((e=>{const t=e[n(0,e.length-1)].message.toUpperCase();document.querySelector(".main__message").innerHTML=`ТЫ <span class="text-danger">${t}</span>`}))}));const n=(e,n)=>{const t=e-.5+Math.random()*(n-e+1);return Math.round(t)}}));